// app/gantt/page.tsx
import React from 'react';
import dynamic from 'next/dynamic';

// load client-only Gantt component dynamically (disable SSR)
const GanttCombined = dynamic(() => import('../../components/GanttCombined'), { ssr: false });

export default function Page() {
  return (
    <main style={{ padding: 20, background: '#f3f4f6', minHeight: '100vh' }}>
      <GanttCombined />
    </main>
  );
}
